import { ChartData, ChartOptions } from 'chart.js';
import { DefaultTheme } from 'styled-components';

interface ChartDataPoint {
  time_open: string;
  rate_close: number;
}

export const prepareChartData = (
  data: ChartDataPoint[],
  theme: DefaultTheme,
): ChartData<'bar'> => ({
  labels: data.map((d) => new Date(d.time_open).toLocaleDateString()),
  datasets: [
    {
      data: data.map((d) => d.rate_close),
      backgroundColor: data.map((d, index) =>
        index > 0 && d.rate_close < data[index - 1].rate_close
          ? theme.colors.chartPositive
          : theme.colors.chartNegative,
      ),
    },
  ],
});

export const getChartOptions = (
  currencyTitle: string,
  theme: DefaultTheme,
): ChartOptions<'bar'> => ({
  responsive: true,
  maintainAspectRatio: false,
  scales: {
    y: {
      title: {
        display: true,
        text: 'Price $',
        color: theme.colors.primaryText,
        font: { size: 14 },
      },
      ticks: {
        display: true,
        color: theme.colors.primaryText,
        font: { size: 12 },
      },
      beginAtZero: false,
    },
    x: {
      title: {
        display: true,
        text: 'Day',
        color: theme.colors.primaryText,
        font: { size: 14 },
      },
      ticks: {
        display: false,
      },
    },
  },
  plugins: {
    tooltip: {
      callbacks: {
        label: (tooltipItem) => `Price ${tooltipItem.raw} $`,
      },
    },
    legend: {
      display: false,
    },
    title: {
      text: currencyTitle,
      display: true,
      color: theme.colors.primaryText,
      font: { size: 16 },
    },
  },
});
