import { ObserverCallback } from '@/types/observer';

class CurrencyChartObserver {
  private observers: ObserverCallback[] = [];

  subscribe(callback: ObserverCallback) {
    this.observers.push(callback);
  }

  unsubscribe(callback: ObserverCallback) {
    this.observers = this.observers.filter((observer) => observer !== callback);
  }

  notify() {
    this.observers.forEach((observer) => observer());
  }
}

export const currencyChartObserver = new CurrencyChartObserver();
