import { CurrencyBarChart } from '@components/CurrencyBarChart';
import { MemoizedCurrencySearch as CurrencySearch } from '@components/CurrencySearch';
import { Toast } from '@components/Modals/Toast';
import { MemoizedValidatedInput as ValidatedInput } from '@components/UI/ValidatedInput';
import {
  fetchCurrencyHistoryThunk,
  setDaysAmount,
  setSelectedCurrency,
} from '@store/slices/currencyHistorySlice';
import { currencyChartObserver } from '@utils/CurrencyChartObserver';
import { validateMaxValue, validateOnlyDigits } from '@utils/validation';
import { Component } from 'react';
import { connect } from 'react-redux';

import { AppDispatch, RootState } from '@/store';

import { daysInputData, toastMessage } from './config';
import { Container, SearchContainer } from './styled';
import { CurrencyChartProps, CurrencyChartState } from './types';

class CurrencyChart extends Component<CurrencyChartProps, CurrencyChartState> {
  state: CurrencyChartState = {
    showToast: false,
  };

  componentDidMount(): void {
    this.fetchInitialData();
    currencyChartObserver.subscribe(this.handleChartComplete);
  }

  componentDidUpdate(prevProps: CurrencyChartProps): void {
    this.checkForDataUpdates(prevProps);
  }

  componentWillUnmount(): void {
    currencyChartObserver.unsubscribe(this.handleChartComplete);
  }

  fetchInitialData = () => {
    const { data, selectedCurrency, daysAmount } = this.props;
    if (!data.length && selectedCurrency && daysAmount) {
      this.fetchChartData(selectedCurrency, daysAmount);
    }
  };

  checkForDataUpdates = (prevProps: CurrencyChartProps) => {
    const { selectedCurrency, daysAmount } = this.props;
    if (
      selectedCurrency !== prevProps.selectedCurrency ||
      daysAmount !== prevProps.daysAmount
    ) {
      this.fetchChartData(selectedCurrency, daysAmount);
    }
  };

  handleCurrencyChange = (currency: string) => {
    this.props.setSelectedCurrency(currency);
  };

  handleAmountChange = (days: string) => {
    this.props.setDaysAmount(days);
  };

  handleChartComplete = () => {
    this.setState({ showToast: true });
  };

  handleToastClose = () => {
    this.setState({ showToast: false });
  };

  fetchChartData = (currency: string | null, days: string) => {
    const daysInt = parseInt(days, 10);
    if (currency && !isNaN(daysInt) && daysInt > 0) {
      this.props.fetchCurrencyHistoryThunk({
        currency: currency,
        days: daysInt,
      });
    }
  };

  render() {
    const { data, isLoading, error, selectedCurrency, daysAmount } = this.props;
    const { showToast } = this.state;

    return (
      <Container>
        <SearchContainer>
          <CurrencySearch
            excludedCurrency=""
            onCurrencyChange={this.handleCurrencyChange}
          />

          <ValidatedInput
            value={daysAmount}
            onValueChange={this.handleAmountChange}
            validationRules={[validateOnlyDigits, validateMaxValue]}
            maxLength={daysInputData.length}
            debounceDelay={daysInputData.delay}
            subscription={daysInputData.text}
          />
        </SearchContainer>

        {data.length > 0 && selectedCurrency && daysAmount && (
          <CurrencyBarChart
            data={data}
            currencyTitle={selectedCurrency}
            isLoading={isLoading}
            error={error}
            onChartComplete={this.handleChartComplete}
          />
        )}

        {showToast && (
          <Toast message={toastMessage} onClose={this.handleToastClose} />
        )}
      </Container>
    );
  }
}

const mapStateToProps = (state: RootState) => ({
  data: state.currencyHistory.data,
  isLoading: state.currencyHistory.isLoading,
  error: state.currencyHistory.error,
  selectedCurrency: state.currencyHistory.selectedCurrency,
  daysAmount: state.currencyHistory.daysAmount,
});

const mapDispatchToProps = (dispatch: AppDispatch) => ({
  fetchCurrencyHistoryThunk: (params: { currency: string; days: number }) =>
    dispatch(fetchCurrencyHistoryThunk(params)),
  setSelectedCurrency: (currency: string) =>
    dispatch(setSelectedCurrency(currency)),
  setDaysAmount: (days: string) => dispatch(setDaysAmount(days)),
});

export default connect(mapStateToProps, mapDispatchToProps)(CurrencyChart);
