import { styled } from 'styled-components';

export const ChartContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  height: ${({ theme }) => theme.size.elementSize.l};

  width: ${({ theme }) => theme.size.elementSize.l};
  width: 100%;

  position: relative;

  canvas {
    position: absolute;
    overflow: auto;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  @media ${({ theme }) => theme.mediaQuery.tablet} {
    height: ${({ theme }) => theme.size.elementSize.m};
  }

  @media ${({ theme }) => theme.mediaQuery.tablet} {
    height: ${({ theme }) => theme.size.elementSize.s};
  }
`;

export const ChartError = styled.div`
  color: ${({ theme }) => theme.colors.primaryText};
  font-size: ${({ theme }) => theme.font.fontSizes.m};
  font-weight: ${({ theme }) => theme.font.fontWeights.bold};

  @media ${({ theme }) => theme.mediaQuery.mobile} {
    font-size: ${({ theme }) => theme.font.fontSizes.xs};
  }
`;
