import { darkTheme, lightTheme, Themes } from '@constants/themes';
import { ThemeContext } from '@context/ThemeContextProvider';
import { getChartOptions, prepareChartData } from '@utils/chartSettings';
import {
  BarElement,
  CategoryScale,
  Chart as ChartJS,
  Legend,
  LinearScale,
  Title,
  Tooltip,
} from 'chart.js';
import { useContext, useEffect, useRef } from 'react';
import { Bar } from 'react-chartjs-2';

import { MainLoader } from '../Loaders/MainLoader';
import { ChartContainer } from './styled';
import { CurrencyBarChartProps } from './types';

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
);

export const CurrencyBarChart = (props: CurrencyBarChartProps) => {
  const { data, currencyTitle, isLoading, error, onChartComplete } = props;
  const isChartRenderedRef = useRef(false);

  const { theme: currentTheme } = useContext(ThemeContext);
  const theme = currentTheme === Themes.DARK ? darkTheme : lightTheme;

  const chartData = prepareChartData(data, theme);
  const chartOptions = getChartOptions(currencyTitle, theme);

  useEffect(() => {
    if (data.length > 0) {
      isChartRenderedRef.current = false;
    }
  }, [data]);

  useEffect(() => {
    if (!isLoading && data.length > 0 && !isChartRenderedRef.current) {
      onChartComplete();
      isChartRenderedRef.current = true;
    }
  }, [isLoading, data.length, onChartComplete]);

  if (isLoading) {
    return (
      <ChartContainer>
        <MainLoader />
      </ChartContainer>
    );
  }

  if (error) {
    return (
      <ChartContainer>
        <p>{error}</p>
      </ChartContainer>
    );
  }

  return (
    <ChartContainer>
      <Bar data={chartData} options={chartOptions} />
    </ChartContainer>
  );
};
