import { useDebounce } from '@hooks/useDebounce';
import { useValidation } from '@hooks/useValidation';
import { ChangeEvent, memo, useCallback, useEffect, useState } from 'react';

import { ErrorMessage, Input, InputContainer, Subscription } from './styled';

interface ValidatedInputProps {
  value: string;
  onValueChange: (value: string) => void;
  validationRules?: ((value: string) => string | null)[];
  maxLength?: number;
  debounceDelay?: number;
  placeholder?: string;
  subscription?: string;
}

const ValidatedInput = (props: ValidatedInputProps) => {
  const {
    value,
    onValueChange,
    validationRules = [],
    maxLength = 10,
    debounceDelay = 300,
    placeholder = '',
    subscription = '',
  } = props;

  const [inputValue, setInputValue] = useState<string>(value);
  const debouncedValue = useDebounce({
    value: inputValue,
    delay: debounceDelay,
  });
  const errorMessage = useValidation(debouncedValue, validationRules);

  const handleChange = useCallback(
    (event: ChangeEvent<HTMLInputElement>) => {
      const newValue = event.target.value;
      if (newValue.length <= maxLength) {
        setInputValue(newValue);
      }
    },
    [maxLength],
  );

  useEffect(() => {
    if (errorMessage === null) {
      onValueChange(debouncedValue);
    }
  }, [debouncedValue, errorMessage, onValueChange]);

  return (
    <InputContainer>
      {errorMessage ? (
        <ErrorMessage>{errorMessage}</ErrorMessage>
      ) : (
        <Subscription>{subscription}</Subscription>
      )}

      <Input
        type="text"
        value={inputValue}
        onChange={handleChange}
        maxLength={maxLength}
        placeholder={placeholder}
      />
    </InputContainer>
  );
};

export const MemoizedValidatedInput = memo(ValidatedInput);
