import {
  validationErrorMessages,
  validationRegex,
} from '@constants/validation';

export const validateOnlyDigits = (value: string): string | null => {
  return validationRegex.onlyDigits.test(value)
    ? null
    : validationErrorMessages.invalidCharacters;
};

export const validateMaxValue = (value: string): string | null => {
  const maxValue = 100;
  const numericValue = parseInt(value, 10);
  return numericValue <= maxValue ? null : validationErrorMessages.overMaxValue;
};
